.dashboard{
    background-image: url('https://images.unsplash.com/photo-1561758033-7e924f619b47?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=334&q=80');
/*     background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.3)),  url('https://images.unsplash.com/photo-1561758033-7e924f619b47?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=334&q=80'); */
    background-repeat: no-repeat;
    min-height: 91.5vh;
    background-attachment: fixed;
    background-position: center center;
    background-size: cover; 
}

.general{
    background-color: rgba(0,0,0,0.8);
    border-color: none;
    border-radius: 5px;
}
.dash-btn{
    margin-top: 15%;
    border: 1px solid #FFFFFF;
    background-color: rgba(0,0,0,0.8);
    color: white;
    border-radius: 5px;
    width: 186px;
    height: 50px;
    font-family: 'Raleway';
    font-style: normal;
    font-weight: 520;
    font-size: 19px;
    margin-left: 3%
}

.dash-btn:hover {
    background-color:#FBE677;
    color: black;
}

.table-select{
    margin-top: 10%;
}
.menu-list{
    margin-left: 5%;
}
.client-info{
    padding-left: 50%;
}
.scroll-cards{
    height: 570px;
    overflow: scroll;
}
.products-order{
    font-size: 15px;
    margin-top: 35%;
}